@import 'fonts.scss';
@import 'var.scss';
@import 'media.scss';

body {
	background-color: #fff;
	font-family: 'Oswald', sans-serif;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	overflow: hidden;
	height: 100vh;
	color: #202124;
}
h1 {
	text-transform: uppercase;
	font-weight: 400;
	font-size: 130px;
	padding-left: 30px;
	color: #202124;
	@media  (max-width: 768px) {
		font-size: 80px;
	}
	@media (max-width: 420px) {
		padding-left: 10px;
	}
}
h2 {
	font-size: 60px;
	margin-top: 0;
	text-transform: uppercase;
	@media  (max-width: 768px) {
		font-size: 36px;
	}
	@media (max-width: 420px) {
		font-size: 26px;
		margin-bottom: 15px;
	}
}
a {
	color: #202124;
	text-decoration: none;
}
p {
	font-size: 16px;
}
.description {
	font-size: 14px;
	@media (max-width: 420px) {
		font-size: 14px;
		margin: 5px 0;
	}
}
.subtitle {
	font-size: 20px;
	@media (max-width: 420px) {
		font-size: 16px;
		margin: 5px 0;
	}
}
.wrapper {
	width: 100vw;
	height: 100vh;
	box-sizing: border-box;
	position: relative;
	display: flex;
	align-items: center;
	background-color: #eeeeee;
	background-image: url(../images/6.png);
	background-size: cover;
	background-attachment: fixed;
}
.btn {
	position: absolute;
	bottom: 40px;
	left: 50%;
	transform: translateX(-50%);
	font-size: 24px;
	color: #000;
	opacity: 0;
	transition: all 0.4s ease 2s;
	text-decoration: none;
	@media (max-width: 420px) {
		font-size: 16px;
	}
	&.active {
		opacity: 1;
	}
	&:before {
		content: '';
		width: 0%;
		height: 1px;
		background-color: #000;
		display: block;
		position: absolute;
		bottom: 0;
		transition: all 0.5s ease;
	}
	&:hover:before {
		width: 100%;
	}
}
.btn-prev {
	position: absolute;
	font-family: 'Oswald', sans-serif;
	bottom: 30px;
	left: 94%;
	visibility: hidden;
	border: none;
	background-color: transparent;
	color: #fff;
	font-size: 20px;
	cursor: pointer;
	text-decoration: underline;
	z-index: 20;
	@media (max-width: 768px) {
		color: #000;
		left: auto;
		right: 10px;
		bottom: 10px;
	}
	@media (max-width: 420px) {
		font-size: 16px;
	}
	&.active {
		visibility: visible;
	}
}


.item {
	width: 50%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 0;
	transition: all 0.6s ease;
	z-index: 10;
	@media  (max-width: 768px) {
		height: 50vh;
		width: 100%;
	}
	&.active {
		top: 0;
		@media  (max-width: 768px) {
			left: 0;
			top: auto;
		}
	}
	&.one {
		background-color: #fff;
		background-image: url(../images/6.jpg);
		background-size: cover;
		left: 0;
		top:-100vh;
		&.active {
			top: 0;
		}
		@media  (max-width: 768px) {
			top:0;
			left: 100%;
			background: #323232;
			&.active {
				left: 0;
				top: 0;
			}
		}
	}
	&.two {
		background-color: #000a12;
		right: 0;
		top: 100vh;
		&.active {
			top: 0;
		}
		@media  (max-width: 768px) {
			top:auto;
			left: -100%;
			bottom: 0;
			background-image: url(../images/6.jpg);
			background-size: cover;
			&.active {
				left: 0;
				top: auto;
			}
		}
	}

	&__wrap {
		width: 400px;
		height: 430px;
		box-sizing: border-box;
		background-color: #eeeeee;
		padding: 60px 50px;
		cursor: pointer;
		transition: all 0.2s ease;
		@media  (max-width: 768px) {
			width: 300px;
			padding: 30px 20px;
		}
		@media (max-width: 420px) {
			width: 250px;
			padding: 30px 18px;
		}
		&:hover {
			transform: scale(1.1);
		}
		&--dark {
			box-shadow: 0 0 10px rgba(0,0,0,0.5);
			@media (max-width: 768px) {
				box-shadow: 0 0 10px rgba(255,255,255,0.5);
			}
		}
		&--white {
			box-shadow: 0 0 10px rgba(255,255,255,0.5);
			@media (max-width: 768px) {
				box-shadow: 0 0 10px rgba(0,0,0,0.5);
			}
		}
	}
	&__img {
		width: 300px;
		height: 300px;
		overflow: hidden;
	}
}

.menu {
	background-color: #000a12;
	box-sizing: border-box;
	color: #ffffff;
	padding: 58px 0 84px;
  z-index: 10000;
  position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	min-height: 100%;
	opacity: 0;
	transform-origin: right top;
	transform: scale(0);
	visibility: hidden;
	transition: all 0.4s ease-in-out;
	display: flex;
	align-items: center;
	justify-content: center;
	&.open {
		opacity: 1;
    visibility: visible;
    transform: scale(1);
	}
	&__list {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-content: center;
		margin: 0 auto;
	}
	&__item {
		font-size: 26px;
		display: flex;
		align-items: center;
		margin-bottom: 30px;
		transform: translateY(40px);
		opacity: 0;
		transition-property: opacity, transform;
		transition-duration: 0.5s, 0.1s;
		transition-timing-function: ease;
		@media (max-width: 768px) {
			font-size: 20px;
		}
		&:first-child {
			transition-delay: 0.3s;
		}
		&:nth-child(2) {
			transition-delay: 0.6s;
		}
		&:nth-child(3) {
			transition-delay: 0.9s;
		}
		&.active {
			transform: translateX(0px);
			opacity: 1;
		}
		a {
			color: #fff;
		}
	}
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 55px;
		height: 50px;
		border: 1px solid #fff;
		margin-right: 36px;
	  border-radius: 50%;
		position: relative;
		cursor: pointer;
		transition: all 0.2s ease;
		@media (max-width: 768px) {
			margin-right: 20px;
		}
		@media (max-width: 420px) {
			width: 40px;
			height: 35px;
		}
		i {
				color: #fff;
				font-size: 18px;
  			transition: all 0.2s ease;
				@media (max-width: 420px) {
					font-size: 14px;
				}
		}
		&:hover {
			background: #fff;
			i {
				 color: #282e33;
			}
			& + span {
				&:before {
					width: 100%;
				}
			}
		}
	}
	span {
		position: relative;
		padding-bottom: 5px;
		&:before {
			content: '';
			width: 0%;
			height: 1px;
			background-color: #fff;
			display: block;
			position: absolute;
			bottom: 0;
			transition: all 0.5s ease;
		}
	}
}

.nav-icon {
	position: absolute;
	display: flex;
	align-items: center;
	top: 30px;
	right: 40px;
	z-index: 10000;
	font-size: 26px;
	cursor: pointer;
	transition: all .3s ease 0s;
	@media (max-width: 768px) {
		top: 10px;
		right: 10px;
	}
	&.active {
		color: #fff;
		transition: all .3s ease;
	}
	&__line {
		height: 2px;
  	width: 36px;
  	background: #000;
  	margin: 3px auto;
  	transition: all 300ms ease;
  	display: block;
		@media (max-width: 768px) {
			width: 32px;
		}
	}
	&__btn {
		display: block;
		margin-right: 20px;
  	&.active {
			.nav-icon__line {
				background-color: #fff;
			}
		  .nav-icon__line:nth-child(1) {
		    transform: rotate(45deg) translate(2px, 4px);
		  }
		  .nav-icon__line:nth-child(2) {
		    transform: rotate(-45deg) translate(0px, -2px);
		  }
  	}
	}
	&.color {
		color: #fff;
		transition: all .3s ease .3s;
		.nav-icon__line {
			background: #fff;
			transition: all .3s ease .3s;
		}
	}
	span {
		text-transform: uppercase;
		font-size: 18px;
		@media (max-width: 768px) {
			font-size: 16px;
		}
	}
}


.title {
  text-transform: uppercase;
	span {
	  opacity: 0;
	  transition: opacity 1300ms;
		&:nth-child(1) {
		  transition-delay:  200ms
		}
		&:nth-child(2)  {
		  transition-delay: 1200ms
		}
		&:nth-child(3)  {
		  transition-delay:  800ms
		}
		&:nth-child(4)  {
		  transition-delay:  300ms
		}
		&:nth-child(5)  {
		  transition-delay:  700ms
		}
		&:nth-child(6)  {
		  transition-delay:  600ms
		}
		&:nth-child(7)  {
		  transition-delay:  400ms
		}
		&:nth-child(8)  {
		  transition-delay:  900ms
		}
		&:nth-child(9)  {
		  transition-delay:  700ms
		}
	}
	&.active span{
		opacity: 1;
	}
}
